import useUser from 'stores/user';
import useHelpers from 'stores/helpers';
import { date, debounce, throttle, format, openURL, uid } from 'quasar';
import FileUploader from 'src/components/Helpers/FilesUploader.vue';
import RenameFolderFile from 'src/components/Dialogs/RenameFolderFile.vue';
const { humanStorageSize } = format;
import { FileUploader_Mixin } from 'src/mixins';
import fnStore from 'stores/fn.store';
import { Helpers_Mixin } from 'src/mixins';
import { checkIfObject } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'FilesPage',

  components: { CardIconBox, FileUploader, RenameFolderFile },

  mixins: [
    // Uploader functions
    FileUploader_Mixin,
    Helpers_Mixin,
  ],

  data() {
    return {
      title: 'Files and Assets',
      searchQuery: '',
      ownerFilter: '',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      tableLoading: false,
      removedFile: null,
      selectedFiles: [],
      visibleColumns: [],
      onRequestDelayed: null,
      orgFiles: [],
      folderfileToRename: null,
      openRenameDialog: false,
      fileToDelete: null,
      openDeleteDialog: false,
      openMultiDeleteDialog: false,
      pagination: {
        page: 1,
        rowsPerPage: 10,
        rowsNumber: 0,
        sortBy: 'updated_at',
        descending: true,
      },
      parentsPath: [],
      columns: [
        {
          name: 'name',
          required: true,
          label: 'Name',
          align: 'left',
          field: (row) => (row.folder ? row.name : row.name),
          format: (val) => `${val}`,
          sortable: true,
        },
        {
          name: 'file_size',
          label: 'File size',
          align: 'left',
          field: (row) => (isNaN(row.file_size) ? '~' : row.file_size),
          format: (val) =>
            val === '~' ? '~' : `${humanStorageSize(Number(val || 0))}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'created_at',
          label: 'Date uploaded',
          align: 'left',
          field: (row) => row.created_at || '~',
          format: (val) =>
            val === '~' ? '~' : `${date.formatDate(val, 'MMM D, YYYY')}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'updated_at',
          label: 'Last updated',
          align: 'left',
          field: (row) => row.updated_at || '~',
          format: (val) =>
            val === '~' ? '~' : `${date.formatDate(val, 'MMM D, YYYY')}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'uploaded_by',
          label: 'Uploaded by',
          align: 'left',
          field: (row) => row.uploaded_by || '~',
          format: (val) =>
            val === '~'
              ? '~'
              : val === this.user.id
              ? 'me'
              : this.selectedOrgUsers?.[val]
              ? `${this.selectedOrgUsers?.[val]?.firstname} ${this.selectedOrgUsers?.[val]?.lastname}`
              : '~',
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
      ],
      orgFilesOriginal: {},
      folder_id: '',
      openURL,
    };
  },

  mounted() {
    this.$root.setMeta({ title: this.title });
    this.visibleColumns = this.columns
      .map((c) => c.name) // Get all the column names
      .filter((e) => !['id'].includes(e)); // Hide some columns initially

    this.onRequestDelayed = throttle(this.onRequest, 1000);
    // Update from params
    if (
      this.route_folder_id ||
      this.route_page ||
      this.route_search ||
      this.route_order_by ||
      this.route_sort_type
    ) {
      if (this.route_folder_id) this.folder_id = parseInt(this.route_folder_id);
      if (this.route_page) this.pagination.page = parseInt(this.route_page);
      if (this.route_search) this.searchQuery = this.route_search;
      if (this.route_order_by) this.pagination.sortBy = this.route_order_by;
      if (this.route_sort_type)
        this.pagination.descending =
          this.route_sort_type?.toLowerCase?.() == 'desc' ? true : false;
    } else {
      this.onRequest({
        pagination: this.pagination,
        filter: { search: this.searchQuery, owner: this.ownerFilter },
      });
    }
  },

  computed: {
    ...mapState(useUser, [
      'user',
      'selectedOrg',
      'selectedOrgFiles',
      'selectedOrgUsers',
      'selectedOrgBrands',
    ]),

    fieldnameMaxWidth() {
      return this.$q.screen.lt.sm
        ? 100
        : this.$q.screen.lt.md
        ? 150
        : this.$q.screen.lt.lg
        ? 225
        : this.$q.screen.lt.xl
        ? 300
        : 350;
    },

    selected_files_only() {
      return this.selectedFiles.filter((e) => !e.folder);
    },

    selected_folders_only() {
      return this.selectedFiles.filter((e) => e.folder);
    },

    route_folder_id() {
      return this.$route.query.folder_id || '';
    },

    route_page() {
      return this.$route.query.page || '';
    },

    route_search() {
      return this.$route.query.search || '';
    },

    route_order_by() {
      return this.$route.query.order_by || '';
    },

    route_sort_type() {
      return this.$route.query.sort_type || '';
    },

    location() {
      return window.location;
    },
  },

  watch: {
    ownerFilter: {
      async handler(val) {
        this.watchRequest({ filter: val || undefined });
      },
    },

    searchQuery: {
      async handler(val) {
        this.watchRequest({ search: val || undefined });
      },
    },

    folder_id: {
      async handler(val) {
        this.watchRequest({ folder_id: val || undefined });
      },
    },

    ['pagination.page']: {
      async handler(val) {
        this.watchRequest({ page: val || undefined });
      },
    },

    ['pagination.sortBy']: {
      async handler(val) {
        this.watchRequest({ order_by: val || undefined });
      },
    },

    ['pagination.descending']: {
      async handler(val) {
        this.watchRequest({ sort_type: val ? 'desc' : 'asc' });
      },
    },

    route_folder_id: {
      async handler(val) {
        if (val !== this.folder_id && this.$route.path == '/files')
          this.folder_id = val;
      },
    },
  },

  methods: {
    ...mapActions(useHelpers, ['uploadFile', 'getFiles']),

    refreshFiles(background = false) {
      this.watchRequest({}, background);
    },

    conditionalDeleteProps(file) {
      let noError =
        (file.folder && file.created_by_id === this.user.id) || !file.folder;
      return {
        messageTitle: noError
          ? `Confirm to delete "${file.name}"?`
          : `Unable to delete "${file.name}"!`,
        cancelText: noError ? 'Cancel' : 'Okay',
        // cancel: noError ? true : false,
        ok: noError ? true : false,
        // okText: noError ? 'Delete' : 'Okay',
      };
    },

    downloadZip(src_file, download_filename) {
      const to_download = src_file?.length ? src_file : this.selectedFiles;
      const label = download_filename || 'download';
      const files = [];
      for (const file of to_download) {
        if (!file.folder) {
          files.push({
            url: file.download_url,
            filename: file.name,
          });
        }
      }
      this.$downloadZip(files, label);
    },

    openUploader() {
      try {
        const upModel = this.$refs['filesUploader'];
        upModel.$el.querySelector('.q-uploader__header').firstChild.click();
      } catch (e) {
        console.warn('File uploader error:', e.message);
      }
    },

    factoryFn(files) {
      return new Promise((resolve, reject) => {
        const token = fnStore.getAuth();
        resolve({
          method: 'POST',
          fieldName: 'uploads[0]',
          url: `${this.$config?.api?.baseURL}/file/organizations/${this.selectedOrg.id}`,
          formFields: [{ name: 'parent_id', value: this.folder_id }],
          headers: [{ name: 'Authorization', value: `Bearer ${token}` }],
          withCredentials: false,
        });
      });
    },

    updateLightBox() {
      setTimeout(() => {
        if (process.env.DEV_MODE) console.log('Updating Lightbox');
        this.lightboxImages = [];
        const images = this.orgFiles.filter((e) =>
          this.checkFileExtensions(e.name, this.$config.supportedDisplayImage)
        );
        images.forEach((img, i) => {
          this.lightboxImages.push({
            src: img.download_url,
            title: img.name,
            alt: img.name,
          });
          const el = document.querySelector(`[file-id="${img.id}"]`);
          if (el)
            el.onclick = () => {
              if (el.attributes?.['file-id']?.value == img.id) {
                this.lightboxIndex = i;
                this.showLightBox = true;
              }
            };
        });
      }, 500);
    },

    fileRenameUpdate(fileInfo) {
      const indexToRename = this.orgFiles.findIndex((e) => e.id == fileInfo.id);
      if (indexToRename > -1) this.orgFiles[indexToRename] = fileInfo;
      this.updateLightBox();
    },

    async deleteSelectedFiles() {
      if (this.selectedFiles?.length) {
        for (const file of this.selectedFiles) {
          this.deleteFilesLocally(file);
        }
      }
    },

    async deleteFilesLocally(file) {
      if (file?.file_info) {
        this.$refs?.['filesUploader']?.removeFile?.(file?.file_info);
        this.removedFile = file?.file_info;
      }

      this.selectedFiles = this.selectedFiles.filter((e) => e.id != file.id);
      let indexToDelete = -1;
      if (!isNaN(file?.id) && (await this.deleteAction(file))) {
        indexToDelete = this.orgFiles.findIndex((e) => e.id == file.id);
      } else {
        indexToDelete = this.orgFiles.findIndex(
          (e) => e.file_info?.__key === file.file_info?.__key
        );
      }

      const { rowsNumber, rowsPerPage } = this.pagination;
      if (indexToDelete !== -1) this.orgFiles.splice(indexToDelete, 1);
      if (rowsNumber - 1 >= rowsPerPage) {
        // Refresh to update pagination
        this.pagination.rowsNumber = rowsNumber - 1;
        this.pagination.pagesNumber = this.pagination.rowsNumber / rowsPerPage;
        this.refreshFiles(true);
      }
      this.updateLightBox();
    },

    uploadedEvent({ files, xhr }) {
      const response = JSON.parse(xhr.response || '{}');
      if (response.success) {
        files.forEach((file, i) => {
          const fileIndex = this.orgFiles.findIndex(
            (e) => e.file_info?.__key === file.__key
          );
          if (fileIndex !== -1) {
            const last_upload = response.data[i];
            const totalDir = this.orgFiles.filter((e) => e.folder).length;
            this.orgFiles.splice(fileIndex, 1, last_upload);
          }
        });
      }
      this.updateLightBox();
    },

    failedEvent({ files, xhr }) {
      const response = JSON.parse(xhr.response || '{}');
      if (!response.success) {
        files.forEach((file) => {
          const fileIndex = this.orgFiles.findIndex(
            (e) => e.file_info?.__key === file.__key
          );
          if (fileIndex !== -1) {
            this.orgFiles[fileIndex].failed = true;
            this.orgFiles[fileIndex].uploading = false;
          }
        });
      }
    },

    filesAddedQued(files) {
      const totalDir = this.orgFiles.filter((e) => e.folder).length;
      const { rowsNumber, pagesNumber, rowsPerPage } = this.pagination;
      if (this.orgFiles.length + files.length > rowsPerPage) {
        // Update pagination
        this.pagination.rowsNumber = rowsNumber + files.length;
        this.pagination.pagesNumber = rowsNumber / rowsPerPage;
      }
      files.forEach((file) => {
        let file_id = uid();
        let created_at = new Date();
        console.warn(file);
        this.orgFiles.splice(totalDir, 0, {
          id: file_id,
          uid: file_id,
          name: file.name,
          folder: false,
          uploading: true,
          file_size: file.size,
          path: file.__img?.src,
          created_at: created_at,
          updated_at: created_at,
          uploaded_by: this.user?.id,
          file_info: file,
        });
      });
      this.updateLightBox();
    },

    async fetchFromServer(query_props = {}) {
      const response = await this.getFiles(query_props);
      const data = response.data || {};
      this.pagination.rowsNumber = data.total || 1;
      this.pagination.pagesNumber = data.last_page || 1;
      const folders = data.data?.folders || [];
      const files = data.data?.files || [];
      const contents = [...folders, ...files];
      this.parentsPath = Array.isArray(data.parents) ? data.parents : [];
      // Add the current active directory
      this.parentsPath.push({
        label: data.info?.name,
        value: data.info?.id,
      });
      return { response: response, contents: response.success ? contents : [] };
    },

    async onRequest(props, background = false) {
      if (!background) this.tableLoading = true;
      const { page, rowsPerPage, sortBy, descending } = props.pagination;
      const filter = props.filter;
      // fetch data from "server"
      const { response, contents } = await this.fetchFromServer({
        folder_id: this.folder_id || undefined,
        filter: filter?.owner || undefined,
        search: filter?.search || undefined,
        page: page || undefined,
        per_page: rowsPerPage || -1,
        order_by: sortBy || undefined,
        sort_type: descending ? 'desc' : 'asc',
      });

      this.orgFiles = contents || [];
      if (response.success) {
        this.orgFiles = contents;
        const { data } = response;
        // Current page
        if (this.pagination.page != data.current_page)
          this.pagination.page = parseInt(data.current_page);
        // Per Page
        if (this.pagination.rowsPerPage != data.per_page)
          this.pagination.rowsPerPage = parseInt(data.per_page);
        // Total Rows
        if (this.pagination.rowsNumber != data.total)
          this.pagination.rowsNumber = parseInt(data.total);
        // Sort By
        if (this.pagination.sortBy != sortBy) this.pagination.sortBy = sortBy;
        // Descending
        if (this.pagination.descending != descending)
          this.pagination.descending = descending;
      }

      this.tableLoading = false;
      this.updateLightBox();
    },

    watchRequest(
      routeQuery,
      background = false,
      clearSelections = true,
      saveScrollPosition = true
    ) {
      this.$refs['filesUploader']?.reset?.();
      if (this.onRequestDelayed)
        this.onRequestDelayed(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery, owner: this.ownerFilter },
          },
          background
        ).then(() => {
          if (clearSelections != false) this.selectedFiles = [];
          const current_query = this.$route.query;
          if (!checkIfObject(routeQuery)) routeQuery = {};
          this.$router.push({
            query: { ...current_query, ...routeQuery },
            params: {
              savePosition: saveScrollPosition == false ? false : true,
            },
          });
        });
    },
  },
});
</script>
